.login {
  display: grid;
  justify-items: center;
  align-items: center;
  &__container {
    min-height: 30vh;
    max-height: 500px;
    border: 1px solid lightgrey;
    padding: 5rem;
    border-radius: 1rem;
    display: grid;
    justify-items: center;
    position: relative;

    &--form {
      display: grid;
      justify-items: center;

      button {
        margin-top: 2rem;
      }
    }

    &--loader {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: grid;
      align-items: center;
      justify-items: center;
      background-color: rgba(255, 255, 255, .9);
    }

    &--error {
      color: firebrick;
    }
  }
}
