.input {
  display: grid;
  grid-row-gap: 0.5rem;
  padding: .3rem;
  &__group {
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-column-gap: 1rem;
   align-items: center;
    label {
      color: rgba(0, 0, 0, .6);
      text-transform: capitalize;
      font-weight: bold;
    }

    input {
      padding: 0.5rem;
      border-radius: 0.3rem;
      border: 1px solid lightgrey;
      outline: none;
    }
  }

  &.error {
    label {
      color: firebrick;
      opacity: 0.7;
    }

    input {
      border: 1px solid firebrick;
    }
  }

  &__error {
    color: firebrick;
    font-size: 0.8rem;
    margin-left: 6rem ;
  }
}
